@use '../config/mixins';

.container {
  max-width: 1264px;
  margin: 0 auto;
  width: 100%;
  padding-left: 16px;
  padding-right: 16px;
  @include mixins.md {
    padding-left: 32px;
    padding-right: 32px;
  }
}

.container-fluid {
  margin: 0 auto;
  width: 100%;
  padding-left: 16px;
  padding-right: 16px;
  @include mixins.md {
    padding-left: 32px;
    padding-right: 32px;
  }
}