@import url("https://fonts.googleapis.com/css2?family=M+PLUS+Rounded+1c:wght@500;700;800;900&display=swap");
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body {
  font-family: BlinkMacSystemFont, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif;
  line-height: 1;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

nav ul {
  list-style: none;
}

ins {
  color: #fff;
  text-decoration: none;
  background-color: rgba(80, 60, 40, 0.5);
}

mark {
  color: #fff;
  font-style: italic;
  font-weight: bold;
  background-color: rgba(80, 60, 40, 0.5);
}

del {
  text-decoration: line-through;
}

abbr[title],
dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
}

input,
button,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

input,
select,
textarea {
  font-size: 100%;
  vertical-align: middle;
}

input {
  border-radius: 0;
  -webkit-appearance: none;
}

textarea {
  display: block;
  resize: vertical;
  width: 100%;
  min-height: 100px;
}

::selection {
  color: #fff;
  background-color: #1bc5bb;
}

b,
strong {
  font-weight: normal;
}

q,
blockquote {
  quotes: none;
}

q:before,
q:after,
blockquote:before,
blockquote:after {
  content: "";
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  text-decoration: none;
  cursor: pointer;
  background: transparent;
}

a:hover {
  text-decoration: underline;
}

a:focus,
button:focus,
input:focus {
  outline: none;
}

a::-moz-focus-inner,
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
}

.cf:before,
.cf:after {
  content: "";
  display: table;
}

.cf:after {
  clear: both;
}

.cf {
  zoom: 1;
}

body {
  line-height: 1.15;
  color: #555555;
  font-family: "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
  font-feature-settings: "palt";
}

img {
  max-width: 100%;
}

a:hover {
  text-decoration: none;
}

button {
  cursor: pointer;
}

.container {
  max-width: 1264px;
  margin: 0 auto;
  width: 100%;
  padding-left: 16px;
  padding-right: 16px;
}
@media only screen and (min-width: 768px) {
  .container {
    padding-left: 32px;
    padding-right: 32px;
  }
}

.container-fluid {
  margin: 0 auto;
  width: 100%;
  padding-left: 16px;
  padding-right: 16px;
}
@media only screen and (min-width: 768px) {
  .container-fluid {
    padding-left: 32px;
    padding-right: 32px;
  }
}

.footer {
  padding-top: 30px;
  padding-bottom: 40px;
  background-color: #555555;
  color: #fff;
}
@media only screen and (min-width: 768px) {
  .footer {
    padding-top: 40px;
    padding-bottom: 60px;
  }
}
.footer__logo {
  font-family: "M PLUS Rounded 1c", "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
  font-size: 32px;
  font-weight: 900;
  letter-spacing: 1.28px;
  opacity: 1;
}

.footer-info {
  margin-top: 24px;
  display: flex;
  flex-direction: column;
  font-size: 14px;
  color: #ccc;
}
@media only screen and (min-width: 768px) {
  .footer-info {
    flex-direction: row;
    justify-content: space-between;
  }
}

.footer-link {
  margin-bottom: 8px;
}
.footer-link__child:not(:last-of-type) {
  margin-right: 8px;
}
@media only screen and (min-width: 768px) {
  .footer-link {
    margin-bottom: 0px;
  }
}

.bnr {
  padding-top: 120px;
  padding-bottom: 120px;
  background-color: #FFF3F2;
}

.heroview {
  background: 68%/cover no-repeat url(../images/kv-bg-image.png);
  height: 100vh;
  padding: 22px 0 0;
  position: relative;
}
.heroview .heroview-filter {
  background-color: rgba(85, 85, 85, 0.2);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: block;
}
.heroview .container {
  position: relative;
}
.heroview .container .heroview-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.heroview .container .heroview-header__left .heroview-header__logo {
  font-size: 32px;
  color: #fff;
  font-family: "M PLUS Rounded 1c", "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
  letter-spacing: 1.28px;
  font-weight: 900;
}
.heroview .container .heroview-header__right .heroview-header__link {
  width: 277px;
  height: 57px;
  font-size: 14px;
  color: #fff;
  border: #fff 1px solid;
  border-radius: 4px;
  padding: 21px 35px;
  display: none;
  letter-spacing: 0.84px;
}
@media only screen and (min-width: 768px) {
  .heroview .container .heroview-header__right .heroview-header__link {
    display: block;
  }
}
.heroview .heroview-contents {
  margin-top: 130px;
}
@media only screen and (min-width: 768px) {
  .heroview .heroview-contents {
    margin-top: 184px;
  }
}
.heroview .heroview-contents .heroview-contents__title {
  font-size: 35px;
  letter-spacing: 1.6px;
  color: #fff;
  line-height: 1.25;
  margin-bottom: 32px;
}
@media only screen and (min-width: 768px) {
  .heroview .heroview-contents .heroview-contents__title {
    font-size: 40px;
  }
}
.heroview .heroview-contents .heroview-contents__text {
  font-size: 14px;
  color: #fff;
  line-height: 1.55;
  margin-bottom: 32px;
}
@media only screen and (min-width: 768px) {
  .heroview .heroview-contents .heroview-contents__text {
    font-size: 16px;
  }
}
.heroview .heroview-contents .heroview-contents__link {
  font-size: 14px;
  color: #fff;
  letter-spacing: 0.84px;
  padding: 21px 42px;
  display: inline-block;
  background-color: #FA776D;
  border-radius: 4px;
}

.case {
  margin-top: 80px;
  margin-bottom: 80px;
}
@media only screen and (min-width: 768px) {
  .case {
    margin-top: 120px;
    margin-bottom: 120px;
  }
}

.caseboxes {
  list-style-type: none;
}

.casebox {
  margin-bottom: 48px;
}
@media only screen and (min-width: 768px) {
  .casebox {
    margin-bottom: 90px;
    position: relative;
    max-width: 1200px;
    height: 460px;
  }
  .casebox:nth-of-type(odd) .casebox-image {
    position: absolute;
    left: 0;
  }
  .casebox:nth-of-type(odd) .casebox-text {
    position: absolute;
    right: 0;
    bottom: 0;
  }
  .casebox:nth-of-type(even) .casebox-image {
    position: absolute;
    right: 0;
  }
  .casebox:nth-of-type(even) .casebox-text {
    position: absolute;
    left: 0;
    bottom: 0;
  }
}

.casebox-image {
  display: flex;
  width: 100%;
  border-radius: 8px;
  overflow: hidden;
}
@media only screen and (min-width: 768px) {
  .casebox-image {
    width: 600px;
  }
}

.casebox-text {
  position: relative;
  padding: 24px 16px;
  background: #FFF;
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.102);
  border-radius: 8px;
  margin-top: -32px;
  margin-left: 16px;
  margin-right: 16px;
}
@media only screen and (min-width: 768px) {
  .casebox-text {
    border-radius: 16px;
    padding: 60px;
    position: absolute;
    width: 55%;
    max-width: 660px;
  }
}

.casebox-text__label {
  font-family: "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
  font-size: 20px;
  color: #555555;
  text-align: left;
  margin-bottom: 16px;
}
@media only screen and (min-width: 768px) {
  .casebox-text__label {
    font-size: 24px;
    margin-bottom: 32px;
  }
}

.casebox-text__description {
  font-family: "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN W3", "Hiragino Sans", Meiryo, sans-serif;
  font-size: 14px;
  line-height: 1.5;
  color: #555555;
  text-align: left;
}
@media only screen and (min-width: 768px) {
  .casebox-text__description {
    font-size: 16px;
  }
}

.feature {
  padding-top: 120px;
  padding-bottom: 120px;
  background-color: #FFF3F2;
}
.feature__title {
  font-size: 32px;
  margin-bottom: 60px;
  font-family: "M PLUS Rounded 1c", "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
  text-align: center;
}
.feature__contents {
  display: grid;
  gap: 30px;
}
@media only screen and (min-width: 768px) {
  .feature__contents {
    gap: 60px;
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

.feature-content {
  background-color: #fff;
  border-radius: 8px;
  overflow: hidden;
  text-align: center;
  font-weight: 600;
  line-height: 1.5;
}
.feature-content__img {
  width: 100%;
}
.feature-content__text {
  padding: 16px;
}
@media only screen and (min-width: 768px) {
  .feature-content__text {
    padding: 24px;
    font-size: 20px;
  }
}

section.usage {
  padding: 120px 0;
}
section.usage .container {
  text-align: center;
}
section.usage .container h2.usage__title {
  font-size: 32px;
  margin-bottom: 60px;
  font-family: "M PLUS Rounded 1c", "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
}
section.usage .container ul.usage-contents {
  display: flex;
  justify-content: space-between;
  max-width: 888px;
  margin: 0 auto;
  flex-direction: column;
}
@media only screen and (min-width: 768px) {
  section.usage .container ul.usage-contents {
    flex-direction: row;
  }
}
section.usage .container ul.usage-contents .usage-content {
  list-style-type: none;
  position: relative;
  margin-bottom: 100px;
}
@media only screen and (min-width: 768px) {
  section.usage .container ul.usage-contents .usage-content {
    margin-bottom: unset;
  }
}
section.usage .container ul.usage-contents .usage-content::after {
  position: absolute;
  content: "";
  margin: auto;
  bottom: -80px;
  display: block;
  width: 18px;
  height: 30px;
  background: center/contain no-repeat url(../images/icon-arrow.svg);
  transform: translateY(-50%) translateX(-50%) rotateZ(90deg);
  left: 50%;
}
@media only screen and (min-width: 768px) {
  section.usage .container ul.usage-contents .usage-content::after {
    top: 50%;
    left: 145%;
    bottom: unset;
    transform: translateY(-50%) translateX(-50%);
  }
}
section.usage .container ul.usage-contents .usage-content__number {
  background-color: #FA776D;
  color: #fff;
  width: 41px;
  height: 41px;
  font-size: 20px;
  border-radius: 50%;
  line-height: 41px;
  margin: 0 auto 19px;
}
section.usage .container ul.usage-contents .usage-content__icon {
  margin: 0 auto 29px;
  height: 100px;
  width: 100px;
  background-color: #FFF3F2;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}
section.usage .container ul.usage-contents .usage-content__icon img {
  width: 36px;
  height: 36px;
}
section.usage .container ul.usage-contents .usage-content__heading {
  font-size: 18px;
  color: #555555;
  font-weight: 600;
}
section.usage .container ul.usage-contents .usage-content:first-child .usage-content__icon img {
  width: 32px;
  height: 30px;
}
section.usage .container ul.usage-contents .usage-content:nth-child(2) .usage-content__icon {
  padding: 10px 0 0 10px;
}
section.usage .container ul.usage-contents .usage-content:nth-child(2) .usage-content__icon img {
  width: 40px;
  height: 45px;
}
section.usage .container ul.usage-contents .usage-content:last-child {
  margin-bottom: 0;
}
section.usage .container ul.usage-contents .usage-content:last-child::after {
  display: none;
}